import Vue from 'vue'
import VueRouter from 'vue-router'
import login from '../views/login.vue'
import home from "@/views/home";
import blog from "@/views/blog/blog";
import blogSave from "@/views/blog/save";
import blogHistory from "@/views/blog/history";
import classify from "@/views/classify/classify";
import classifySave from "@/views/classify/save";
import classifyHistory from "@/views/classify/history";
import comment from "@/views/comment/comment";
import commentHistory from "@/views/comment/history";
import tag from "@/views/tag/tag";
import tagSave from "@/views/tag/save";
import tagHistory from "@/views/tag/history";
import userHistory from "@/views/user/history";
import test from "@/views/test";
import blogComment from "@/views/comment/blogComment";
import userInfo from "@/views/user/userInfo";



import user from "@/views/user/user";

Vue.use(VueRouter)
//push
const originalPush = VueRouter.prototype.push
//push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}
const routes = [
  {
    path: '/',
    name: 'login',
    component: login,
    hidden:true,
    meta:{
      title:"",
      noNeedLogin:true  //
    }
  },
  {
    path: '/home',
    name: '',
    component: home,
    children:[
      {
        path: '/blog',
        name: '',
        component: blog
      },
      {
        path: '/blogSave',
        name: '',
        component: blogSave
      },
      {
        path: '/blogEdit/:id',
        name: '',
        component: blogSave
      },
      {
        path: '/blogHistory',
        name: '',
        component: blogHistory
      },
    ]
  },
  {
    path: '/home',
    name: '',
    component: home,
    children:[
      {
        path: '/classify',
        name: '',
        component: classify
      },
      {
        path: '/classifySave',
        name: '',
        component: classifySave
      },
      {
        path: '/classifyHistory',
        name: '',
        component: classifyHistory
      }
    ]
  },
  {
    path: '/home',
    name: '',
    component: home,
    children:[
      {
        path: '/tag',
        name: '',
        component: tag
      },
      {
        path: '/tagSave',
        name: '',
        component: tagSave
      },
      {
        path: '/tagHistory',
        name: '',
        component: tagHistory
      },
    ]
  },
  {
    path: '/home',
    name: '',
    component: home,
    children:[
      {
        path: '/comment',
        name: '',
        component: comment
      },
      {
        path: '/commentHistory',
        name: '',
        component: commentHistory
      },
      {
        path: '/blogComment/:id',
        name: '',
        component: blogComment
      }
    ]
  },
  {
    path: '/home',
    name: '',
    component: home,
    children: [
      {
        path: '/user',
        name: '',
        component: user
      },
      {
        path: '/userHistory',
        name: '',
        component: userHistory
      },
      {
        path: '/userInfo',
        name: '',
        component: userInfo
      }
    ]
  },
  {
    path: '/home',
    name: '',
    component: home,
    children: [
      {
        path: '/test',
        name: '',
        component: test
      }
    ]
  }
]

const router = new VueRouter({
  mode: 'history',
  routes
})

export default router
