<template>
  <div>
    <h2></h2>
    <!---->
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item>
        <el-input v-model="blogQuery.title" placeholder=""/>
      </el-form-item>

      <el-form-item>
        <el-input v-model="blogQuery.username" placeholder=""/>
      </el-form-item>

      <el-form-item>
        <el-select v-model="blogQuery.status" clearable placeholder="">
          <el-option :value="1" label=""/>
          <el-option :value="0" label=""/>
        </el-select>
      </el-form-item>
      <br>
      <el-form-item>
        <el-date-picker
            v-model="blogQuery.begin"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
        />
      </el-form-item>
      <el-form-item>
        <el-date-picker
            v-model="blogQuery.end"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
        />
      </el-form-item>

      <el-button type="primary" icon="el-icon-search" @click="getList()"></el-button>
      <el-button type="default" @click="resetData()"></el-button>
      <el-button type="danger" @click="removeBatch"></el-button>
    </el-form>

    <!--  -->
    <el-table ref="select" :data="blogList" border fit highlight-current-row @cell-click="cellClick" @selection-change="selectionChange">
      <el-table-column
          type="selection"
          width="55">
      </el-table-column>
      <el-table-column label="" width="70" align="center">
        <template slot-scope="scope">{{ (page - 1) * limit + scope.$index + 1 }}</template>
      </el-table-column>

      <el-table-column prop="title" label="" />

      <el-table-column label="" width="80">
        <template slot-scope="scope">{{ scope.row.tpublish ? '' : '' }}</template>
      </el-table-column>

      <el-table-column prop="username" label="" width="100"/>

      <el-table-column prop="gmtCreate" sortable label="" width="160"/>
      <el-table-column prop="gmtModified" sortable label="" width="160"/>

      <el-table-column label="" width="300" align="center">
        <template slot-scope="scope">
          <router-link :to="'/blogComment/'+scope.row.id">
            <el-button type="primary" size="mini" icon="el-icon-chat-line-round" style="margin-right: 10px"></el-button>
          </router-link>
          <router-link :to="'/blogEdit/'+scope.row.id">
            <el-button type="primary" size="mini" icon="el-icon-edit" style="margin-right: 10px"></el-button>
          </router-link>
          <el-button
              type="danger"
              size="mini"
              icon="el-icon-delete"
              @click="removeDataById(scope.row.id)"
          >
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-pagination
        :current-page="page"
        :page-size="limit"
        :total="total"
        style="padding: 30px 0; text-align: center;"
        layout="prev, pager, next, jumper"
        @current-change="getList"
    />
  </div>
</template>

<script>
export default {
  name: "blog",
  data(){
    return{
      blogList:null,
      total:0,
      page:1,
      limit:8,
      blogQuery:{
        title:"",
        classify:"",
        tagId:"",
        status:null,
        username:"",
        type:"",
        begin:"",
        end:""
      },
      selections:[],
    }
  },
  created() {
    this.getList()
  },
  methods:{
    removeBatch(){
      this.$confirm("","",{
        confirmButtonText:"",
        cancelButtonText:"",
        type:"warning"
      }).then(()=>{
        this.postRequest(`/tora/blog/deleteBatch`,this.selections).then(response=>{
          if ((response.data.total%this.limit)==0){
            this.getList(this.page-1);
          } else {
            this.getList(this.page);
          }
        })
      })
    },
    selectionChange(val){
      this.selections = val
      console.log(this.selections)
    },
    cellClick(row){
      this.$refs.select.toggleRowSelection(row)
    },
    getList(page = 1){
      this.page = page
      this.postRequest(`/tora/blog/queryBlogPage/${this.page}/${this.limit}`,this.blogQuery).then(res=>{
        this.blogList = res.data.records
        this.total = res.data.total
      }).catch(res=>{
        this.blogQuery={
          title:"",
          classify:"",
          tagId:"",
          username:"",
          type:"",
          begin:"",
          end:""
        };
      })
    },
    //
    resetData() {
      this.blogQuery = {
        title:"",
        classify:"",
        tagId:"",
        username:"",
        type:"",
        begin:"",
        end:""
      };
      this.getList();
    },
    //
    removeDataById(id) {
      //
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.deleteRequest(`/tora/blog/deleteBlogById/${id}`).then(response => {
          //
          if ((response.data.total%this.limit)==0){
            this.getList(this.page-1);
          } else {
            this.getList(this.page);
          }
        });
      });
    }
  }
}
</script>

<style scoped>

</style>