<template>
  <div>
    <h3></h3>

    <el-form :inline="true" class="demo-form-inline">
      <el-form-item>
        <el-input v-model="commentQuery.title" placeholder=""/>
      </el-form-item>

      <el-form-item>
        <el-input v-model="commentQuery.username" placeholder=""/>
      </el-form-item>

      <el-form-item>
        <el-input v-model="commentQuery.keyWord" placeholder=""/>
      </el-form-item>
      <br>
      <el-form-item>
        <el-select v-model="commentQuery.status" clearable placeholder="">
          <el-option :value="1" label=""/>
          <el-option :value="0" label=""/>
          <el-option :value="-1" label=""/>
        </el-select>
      </el-form-item>

      <el-form-item>
        <el-date-picker
            v-model="commentQuery.begin"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
        />
      </el-form-item>

      <el-button type="primary" icon="el-icon-search" @click="getAllComment()"></el-button>
      <el-button type="default" @click="resetData()"></el-button>
      <el-button type="default" @click="resumeBatch()"></el-button>
    </el-form>

    <el-table ref="select" :data="commentList" border fit highlight-current-row @cell-click="cellClick" @selection-change="selectionChange">
      <el-table-column
          type="selection"
          width="55">
      </el-table-column>
      <el-table-column label="" width="70" align="center">
        <template slot-scope="scope">{{ (page - 1) * limit + scope.$index + 1 }}</template>
      </el-table-column>

      <el-table-column prop="title" label="" width="160" />

      <el-table-column label="" width="80">
        <template slot-scope="scope">{{ scope.row.tpublish == 1 ? '' : scope.row.tpublish == 0 ? "" : "" }}</template>
      </el-table-column>

      <el-table-column prop="content" label=""></el-table-column>

      <el-table-column prop="username" label="" width="100"/>

      <el-table-column prop="gmtCreate" sortable label="" width="160"/>

      <el-table-column label="" width="300" align="center">
        <template slot-scope="scope">
          <el-button
              type="success"
              size="mini"
              icon="el-icon-check"
              @click="resume(scope.row)"
          >
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-pagination
        :current-page="page"
        :page-size="limit"
        :total="total"
        style="padding: 30px 0; text-align: center;"
        layout="prev, pager, next, jumper"
        @current-change="getAllComment"
    />
  </div>
</template>

<script>
export default {
  name: "history",
  data(){
    return{
      commentList:[],
      page:1,
      limit:15,
      total:0,
      commentQuery:{
        title:"",
        username:"",
        keyWord:"",
        status:null,
        begin:""
      },
      selections:[]
    }
  },
  created() {
    this.getAllComment()
  },
  methods:{
    resume(row){
      this.$confirm("","",{
        confirmButtonText:"",
        cancelButtonText:"",
        type:"warning"
      }).then(()=>{
        this.getRequest(`/tora/comment/resume/${row.id}`).then(()=>{
          this.getAllComment(this.page)
        })
      })
    },
    resumeBatch(){
      this.$confirm("","",{
        confirmButtonText:"",
        cancelButtonText:"",
        type:"warning"
      }).then(()=>{
        this.postRequest(`/tora/comment/resumeBatch`,this.selections).then(()=>{
          this.getAllComment(this.page)
        })
      })
    },
    resetData(){
      this.getAllComment(this.page);
    },
    selectionChange(row){
      this.selections = row
      console.log(this.selections)
    },
    cellClick(row){
      this.$refs.select.toggleRowSelection(row)
    },
    getAllComment(page = 1){
      this.page = page
      this.postRequest(`/tora/comment/queryDeletedPage/${this.page}/${this.limit}`,this.commentQuery).then(res=>{
        this.commentList = res.data.records
        this.total = res.data.total
      })
    }
  }
}
</script>

<style scoped>

</style>