<template>
  <div>
    <h2></h2>
    <!---->
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item>
        <el-input v-model="blogQuery.title" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-date-picker
            v-model="blogQuery.begin"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
        />
      </el-form-item>

      <el-button type="primary" icon="el-icon-search" @click="getList()"></el-button>
      <el-button type="default" @click="resetData()"></el-button>
      <el-button type="success" @click="resumeBatch()"></el-button>
    </el-form>

    <!--  -->
    <el-table ref="selection" :data="blogList" border fit highlight-current-row @cell-click="cellClick" @selection-change="selectionChange">
      <el-table-column
        type="selection"
        width="55"
      >
      </el-table-column>
      <el-table-column label="" width="70" align="center">
        <template slot-scope="scope">{{ (page - 1) * limit + scope.$index + 1 }}</template>
      </el-table-column>

      <el-table-column prop="title" label=""/>

      <el-table-column label="" width="80">
        <template slot-scope="scope">{{ scope.row.status === 1 ? '' : '' }}</template>
      </el-table-column>

      <el-table-column prop="username" label="" width="120"/>

      <el-table-column prop="gmtCreate" label="" width="160"/>
      <el-table-column prop="gmtModified" label="" width="160"/>

      <el-table-column label="" width="200" align="center">
        <template slot-scope="scope">
          <el-button
              type="success"
              size="mini"
              icon="el-icon-check"
              @click="removeDataById(scope.row.id)"
          >
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-pagination
        :current-page="page"
        :page-size="limit"
        :total="total"
        style="padding: 30px 0; text-align: center;"
        layout="prev, pager, next, jumper"
        @current-change="getList"
    />
  </div>
</template>

<script>
export default {
  name: "blog",
  data(){
    return{
      blogList:null,
      total:0,
      page:1,
      limit:5,
      blogQuery:{
        title:"",
        begin:""
      },
      selections:[]
    }
  },
  created() {
    this.getList()
  },
  methods:{
    resumeBatch(){
      this.$confirm("","",{
        confirmButtonText:"",
        cancelButtonText:"",
        type:"warning"
      }).then(res=>{
        this.postRequest(`/tora/blog/resumeBatch`,this.selections).then(response=>{
          if ((response.data.total%this.limit)==0){
            this.getList(this.page-1);
          } else {
            this.getList(this.page);
          }
        })
      })
    },
    selectionChange(val) {
      this.selections = val
    },
    cellClick(row){
      this.$refs.selection.toggleRowSelection(row)
    },
    getList(page = 1){
      this.page = page
      this.postRequest(`/tora/blog/queryHistoryPage/${this.page}/${this.limit}`,this.blogQuery).then(res=>{
        this.blogList = res.data.records
        this.total = res.data.total
      }).catch(res=>{
        this.blogQuery={
          title:"",
          classify:"",
          tagId:"",
          username:"",
          type:"",
          begin:"",
          end:""
        }
      })
    },
    //
    resetData() {
      this.blogQuery = {
        title:"",
        classify:"",
        tagId:"",
        username:"",
        type:"",
        begin:"",
        end:""
      };
      this.getList();
    },
    //
    removeDataById(id) {
      //
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.getRequest(`/tora/blog/resumeById/${id}`).then(response => {
          //
          if ((response.data.total%this.limit)==0){
            this.getList(this.page-1);
          }else{
            this.getList(this.page);
          }
        });
      });
    }
  }
}
</script>

